// stylelint-disable comment-whitespace-inside, comment-empty-line-before

/*------------------------------------*\
    #VARIABLES
\*------------------------------------*/
$root: 8px;

// Spacing Defaults
$space-xs: ($root / 2);
$space-sm: $root;
$space-md: ($root * 2);
$space-lg: ($root * 4);
$space-xl: ($root * 8);
$space-xxl: ($root * 16);

$font-size-base: 16px;
$font-weight-light: 300;
$font-weight-base: 400;
$font-weight-medium: 500;
$font-weight-semi-bold: 600;
$font-weight-bold: 700;
$line-height-base: 1.4;
$line-height-heading: 1.3;
$type-scale: 1.25;
$opacity: 0.8;
$input-height: $root * 5;

$enable-hover-media-query:  false !default;

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
);

/*------------------------------------*\
    #COLORS
\*------------------------------------*/

/*
 * 1) Color variables involve a two-tier system to keep colors in sync. The
 *    first tier defines the brand and neutral colors, and the second tier
 *    defines the application of those colors.
 */

/*
 * Brand Colors
 * 1) Brand color variables use English names to define colors to make palettes easier to
 *    identify. Names are generated using this tool: http://chir.ag/projects/name-that-color/
 * 2) Brand color variables should typically not be used directly in Sass partials. Instead, use
 *    second-tier application colors.
 *
 */
$color-brand: #252525;

/*
 * Neutral Colors
 * 1) Neutral color variables use a numbered system to define variables for better maintainability.
 *    "$color-gray-10" means "10% gray", which is a gray closer to white than to black. For more info
 *    read https://medium.com/eightshapes-llc/color-in-design-systems-a1c80f65fa3#.brevrq67p
 */
$color-white: #fff;
$color-gray-50: #ebebeb;
$color-gray-65: #ccc;
$color-gray-85: #959595;
$color-gray-95: #363636;
$color-black: #000;

/**
 * Utility Colors
 * 1) Utility colors are colors used to provide feedback, such as alert messages,
 * form validation, etc.
 */
$color-primary: $color-brand;
$color-secondary: #32c5d2;
$color-danger: #b12a0b;

/*
  * Text Colors
  * 1) These are non-linked text colors, such as body copy.
  */
$color-text: $color-black;
$color-text-subtle: $color-gray-85;
$color-text-light: $color-gray-50;

/*
 * Heading Colors
 * 1) These are non-linked heading colors, such as h2, h3, etc.
 */
$color-heading: $color-black;

/*
 * Text Link Colors
 * 1) These are for linked text, such as a link found within an article,
 *    navigation link, footer link, etc.
 */
$color-text-link: $color-brand;

/*
 * Border Colors
 * 1) Border colors are used as decoration or dividers, such as as a seperator
 *    for navigation items, block borders, etc.
 */
$color-border: $color-gray-50;

/*
 * Body Colors
 */
$color-body-bg: $color-white;

/*------------------------------------*\
    #TYPOGRAPHY
\*------------------------------------*/

$font-family-base: "Poppins", sans-serif;
$font-secondary: "Playfair Display", serif;

$font-size-h6: ($font-size-base / $type-scale);
$font-size-h5: $font-size-base;
$font-size-h4: ($font-size-base * $type-scale);
$font-size-h3: ($font-size-base * $type-scale * $type-scale);
$font-size-h2: ($font-size-base * $type-scale * $type-scale * $type-scale);
$font-size-h1: ($font-size-base * $type-scale * $type-scale * $type-scale * $type-scale);

$font-size-small: $font-size-h6;

/*------------------------------------*\
    #LAYOUT
\*------------------------------------*/
$l-max-width: 80em;

/*------------------------------------*\
    #BORDERS
\*------------------------------------*/

/**
 * Border Width
 */
$border: 1px;
$border-med: $space-xs;
$border-thick: $space-sm;

/**
 * Border Radius
 */
$border-radius: $space-xs;
$border-radius-large: $space-md;
$border-radius-xlarge: $space-lg;

/*------------------------------------*\
    #BORDERS
\*------------------------------------*/

$table-border-width: 1px;

/*------------------------------------*\
    #ANIMATION
\*------------------------------------*/

/**
 * Animation Duration
 */
$anim-speed-quick: 0.2s;

/**
 * Animation Easing
 */
$anim-ease: ease;

/*------------------------------------*\
    #MEDIA QUERIES
\*------------------------------------*/

@custom-media --small-only (max-width: 575px);
@custom-media --medium-up (min-width: 576px);
@custom-media --large-up (min-width: 992px);

/*------------------------------------*\
    #Z-INDEX
\*------------------------------------*/

$z-index-low: 0;
$z-index-base: 1;
